import React                                        from 'react';
import PropTypes                                    from 'prop-types';



/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';

import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';










/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../_abstract/c.abstract';










import './s.servicePackage.detail.step2.css';







export default class ComponentServicePackageDetailStep2 extends ComponentAbstract {


    static propTypes = {
        data                : PropTypes.object

        , form              : PropTypes.object

        , onClickNext       : PropTypes.func
        , onClickBack       : PropTypes.func
    }


    static defaultProps = {
        data                : {}

        , form              : {}

        , onClickNext       : () => {}
        , onClickBack       : () => {}
    }









    constructor( props ) {
        super( props )
    }










    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {
        return (
                <div>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 2 } >
                                Service Package Code
                            </Col>

                            <Col md={ 10 } >
                                { this.props.form.code }
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 2 } >
                                Service Package Name
                            </Col>

                            <Col md={ 10 } >
                                { this.props.form.name }
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 2 } >
                                Description
                            </Col>

                            <Col md={ 10 } >
                                { this.props.form.description }
                            </Col>
                        </Row>
                    </Grid>

                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 2 } >
                                Type
                            </Col>

                            <Col md={ 10 } >
                                { this.props.form.type }
                            </Col>
                        </Row>
                    </Grid>


                    <Grid fluid>
                        <Row className="show-grid">
                            <Col md={ 12 } >

                                <div className={ ' service-package-detail-textAlign-center service-package-detail-paddingBottom-10 service-package-detail-paddingTop-10 ' }>
                                    <RaisedButton
                                                    className={ ' service-package-detail-float-left ' }
                                                    label={ "Back" }
                                                    onClick={ () => {
                                                        this.props.onClickBack();
                                                    } }
                                   />



                                   <RaisedButton
                                                   className={ ' service-package-detail-float-right ' }
                                                   label={ "Confirm" }
                                                   onClick={ () => {
                                                       this.props.onClickNext();
                                                   } }
                                  />
                               </div>

                            </Col>
                        </Row>
                    </Grid>

                </div>
        )
    }
}



