import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionServicePackage , { ReduxEnumServicePackage }  from '../../../_redux/action/reduxAction.servicePackage';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';










/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomAlert                         from '../../_component/alert/cc.alert';
import ComponentCustomLoading                       from '../../_component/loading/cc.loading';


import ComponentServicePackageDetailStep1           from './step1/c.servicePackage.detail.step1';
import ComponentServicePackageDetailStep2           from './step2/c.servicePackage.detail.step2';
import ComponentServicePackageDetailStep3           from './step3/c.servicePackage.detail.step3';








import './s.servicePackage.detail.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxServicePackage' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumServicePackage.UPDATE_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdatePreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumServicePackage.UPDATE_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];




                        } else if ( ReduxEnumServicePackage.ADD_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestAddPreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumServicePackage.ADD_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestAddConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];



                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentServicePackageDetail extends ComponentAbstract {





            constructor( props ) {
                super( props )

                this.state = {

                    step : 0


                    , isAlert           : false
                    , alert             : {
                        title               : ''
                        , message           : ''
                    }


                    , data : { }


                    , form : {
                    }


                    , result : {
                        preview             : {}
                        , confirm           : {}
                    }

                }

            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestUpdatePreview() {
                console.log( 'ComponentServicePackageDetail : requestUpdatePreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionServicePackage.rUpdatePreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdatePreviewDone( result ) {
                console.log( 'ComponentServicePackageDetail : requestUpdatePreviewDone : ***  : result => ' , result );

                this.setState(
                    {
                        isLoading : false
                        , result : result
                    }
                );
                this.dispatch( ReduxActionServicePackage.rRequestEnd( ) );
            }


            requestUpdateConfirm() {
                console.log( 'ComponentServicePackageDetail : requestUpdateConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionServicePackage.rUpdateConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdateConfirmDone( result ) {
                console.log( 'ComponentServicePackageDetail : requestUpdateConfirmDone : ***  : result => ' , result );

                this.setState(
                    {
                        isLoading : false
                        , result : result
                    }
                );
                this.dispatch( ReduxActionServicePackage.rRequestEnd( ) );
            }




            requestAddPreview() {
                console.log( 'ComponentServicePackageDetail : requestAddPreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionServicePackage.rAddPreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestAddPreviewDone( result ) {
                console.log( 'ComponentServicePackageDetail : requestAddPreviewDone : ***  : result => ' , result );

                this.setState(
                    {
                        isLoading : false
                        , result : result
                    }
                );
                this.dispatch( ReduxActionServicePackage.rRequestEnd( ) );
            }


            requestAddConfirm() {
                console.log( 'ComponentServicePackageDetail : requestAddConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionServicePackage.rAddConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestAddConfirmDone( result ) {
                console.log( 'ComponentServicePackageDetail : requestAddConfirmDone : ***  : result => ' , result );

                this.setState(
                    {
                        isLoading : false
                        , result : result
                    }
                );
                this.dispatch( ReduxActionServicePackage.rRequestEnd( ) );
            }
















            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>

                            <div className={ ' service-package-detail-padding-10 ' }>
                                <h3> Service Package Detail </h3>
                            </div>





                            <div
                                    className={ ' service-package-detail-margin-auto service-package-detail-paddingTop-20 service-package-detail-paddingBottom-20  service-package-detail-maxWidth-900 ' }>
                                <Stepper activeStep={ this.state.step }>

                                    <Step>
                                        <StepLabel> Edit Record </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Preview </StepLabel>
                                    </Step>

                                    <Step>
                                        <StepLabel> Confirmation </StepLabel>
                                    </Step>

                                </Stepper>
                            </div>





                            <div>
                                {
                                    ( () => {

                                        switch ( this.state.step ) {

                                            case 0:

                                                return (
                                                    <ComponentServicePackageDetailStep1
                                                        data={ this.state.data }

                                                        form={ this.state.form }


                                                        onChangeForm={
                                                            ( key , value ) => {

                                                                let form = this.state.form;
                                                                form[ key ] = value;
                                                                this.setState( { form : form } );

                                                            }
                                                        }

                                                        onClickNext={ () => {
                                                            this.setState( { step : 1 } );
                                                        } }
                                                        onClickBack={ () => {

                                                            HelperNavigate.route(
                                                                HelperNavigate.mapPath( HelperNavigateENUM.SERVICE_PACKAGE_LIST )
                                                            );

                                                        } }
                                                    />
                                                );

                                                break;

                                            case 1:

                                                return (
                                                    <ComponentServicePackageDetailStep2
                                                        data={ this.state.data }

                                                        form={ this.state.form }


                                                        onClickBack={ () => {
                                                            this.setState( { step : 0 } );
                                                        } }
                                                        onClickNext={ () => {
                                                            this.setState( { step : 2 } );
                                                        } }
                                                    />
                                                );

                                                break;

                                            case 2:

                                                return (
                                                    <ComponentServicePackageDetailStep3
                                                        data={ this.state.data }

                                                        form={ this.state.form }


                                                        onClickBack={ () => {
                                                            this.setState( { step : 1 } );
                                                        } }
                                                        onClickNext={ () => {

                                                        } }
                                                    />
                                                );

                                                break;

                                            default:
                                                return null;
                                        }

                                    } )()
                                }
                            </div>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />




                            <ComponentCustomAlert

                                    onClose={ () => {
                                        HelperNavigate.route(
                                            HelperNavigate.mapPath( HelperNavigateENUM.SERVICE_PACKAGE_LIST )
                                        );
                                    } }

                                    title={ this.state.alert.title }
                                    msg={ this.state.alert.message }
                                    alertType={ 'success' }

                                    visible={ this.state.isAlert }

                            />


                        </div>
                )
            }
        }

    )



